/**=====================
    60. RTL CSS Start
==========================**/

.rtl {
    .page-wrapper {
        &.compact-wrapper {
            .page-body-wrapper {
                header {
                    &.main-nav {
                        text-align: right;
                        transition: 0.5s;
                        border-left: 1px solid $light-gray;
                        border-right: unset;
                        &.close_icon {
                            margin-right: 0;
                            margin-left: unset;
                            transform: translate(290px);
                            ~.page-body {
                                margin-right: 0;
                                margin-left: unset;
                                transition: 0.5s;
                            }
                            ~footer {
                                margin-left: 0;
                                transition: 0.5s;
                            }
                            .main-navbar {
                                .nav-menu {
                                    >li {
                                        a {
                                            svg {
                                                float: left;
                                                margin-right: 10px;
                                            }
                                        }
                                    }
                                }
                            }
                            .according-menu {
                                display: none !important;
                            }
                        }
                        &:hover {
                            transform: translate(0px);
                            .main-navbar {
                                .nav-menu {
                                    >li {
                                        a {
                                            svg {
                                                float: none;
                                                // margin-right: 15px;
                                            }
                                        }
                                    }
                                }
                            }
                            .according-menu {
                                display: block;
                            }
                        }
                        .main-navbar {
                            .nav-menu {
                                &>li {
                                    a {
                                        .sub-arrow {
                                            left: 20px;
                                            right: unset;
                                        }
                                    }
                                }
                            }
                        }
                        .sidebar-user {
                            ul {
                                li {
                                    +li {
                                        &::before {
                                            right: -14px;
                                            left: unset;
                                        }
                                    }
                                }
                            }
                            .setting-primary {
                                right: unset;
                                left: 35px;
                            }
                        }
                    }
                }
                .page-body {
                    margin-left: unset;
                    margin-right: 290px;
                }
                .main-nav {
                    text-align: right;
                    .main-navbar {
                        .nav-menu {
                            >li {
                                a {
                                    svg {
                                        margin-right: unset;
                                        margin-left: 15px;
                                    }
                                }
                            }
                        }
                    }
                }
                .according-menu {
                    right: unset;
                    left: 15px;
                }
            }
        }
        &.compact-sidebar {
            &.compact-wrapper {
                .page-body-wrapper {
                    header {
                        &.main-nav {
                            .main-navbar {
                                .nav-menu {
                                    .dropdown {
                                        .menu-title {
                                            &.active {
                                                ~.menu-content {
                                                    left: unset;
                                                    right: 150px;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .page-body {
                        margin-right: 150px;
                        margin-left: unset;
                    }
                }
            }
        }
        &.modern-sidebar {
            &.compact-wrapper {
                footer {
                    margin-left: 30px;
                }
                header {
                    &.main-nav {
                        &.close_icon {
                            ~footer {
                                margin-left: 30px;
                            }
                        }
                    }
                }
            }
        }
    }
    .main-navbar {
        ul.nav-pills {
            .nav-item {
                &:last-child {
                    border-right: 1px solid $light-gray;
                    border-left: unset;
                }
            }
        }
    }
    // product-page
    .product-social {
        li {
            &:nth-child(n+2) {
                margin-left: unset;
                margin-right: 10px;
            }
        }
    }
    ul {
        &.pro-services {
            li {
                svg {
                    margin-left: 15px;
                    margin-right: unset;
                }
            }
        }
    }
    .filter-block {
        ul {
            li {
                .form-check {
                    padding-right: unset;
                    .form-check-input {
                        float: right;
                        margin-left: 15px;
                    }
                }
            }
        }
    }
    // space class //
    .ms-1 {
        margin-left: unset;
        margin-right: 0.25rem !important;
    }
    .f-right {
        float: left;
    }
    // file manager css
    .file-content {
        .form-inline {
            i {
                padding-right: unset;
                padding-left: 10px;
            }
        }
    }
    .file-sidebar {
