/**=====================
   55. theme layout CSS Start
==========================**/

//Sidebar color scss
$color1: #405365;
$color2: #1C1D1E;
$color3: #0c0c3c;
$color4: #0747a6;
$color5: #4d3096;
@each $bg-name,
$bg-color in (color1, $color1),
(color2, $color2),
(color3, $color3),
(color4, $color4),
(color5, $color5),
(light, $light-gray),
(dark, #2f3c4e) {
    .bg-#{$bg-name} {
        background-color: $bg-color;
    }
}

$i:1;
@while $i<=6 {
    .bg-pattern#{$i} {
        background-image: url(../images/sidebar-pattern/#{$i}.png);
        background-color: #2f3c4e;
    }
    .bg-img#{$i} {
        background-image: url(../images/sidebar-image/#{$i}.jpg);
    }
    $i:$i+1;
}

//Sidebar settings
.page-wrapper.horizontal-wrapper {
    .page-body-wrapper {
        .page-sidebar {
            &[sidebar-layout="border-sidebar"] {
                .sidebar-header {
                    border-bottom: 1px solid rgba(124, 124, 124, 0.15);
                    transition: all 0.3s ease;
                }
            }
            &[sidebar-layout="iconcolor-sidebar"] {
                .sidebar-menu {
                    li {
                        $colors: $success-color, $secondary-color, $primary-color, $danger-color, $light-font, $warning-color, $light-gray;
                        $repeat: 100;
                        @for $i from 1 through $repeat {
                            &:nth-child(#{length($colors)}n+#{$i}) {
                                svg {
                                    color: lighten(nth($colors, random(length($colors))), 15%);
                                }
                                a {
                                    &:hover {
                                        >span {
                                            color: lighten(nth($colors, random(length($colors))), 15%);
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.light-sidebar {
                    .sidebar-menu {
                        li {
                            $colors: $success-color, $secondary-color, $primary-color, $danger-color, $light-font, $warning-color, $dark-color;
                            $repeat: 100;
                            @for $i from 1 through $repeat {
                                &:nth-child(#{length($colors)}n+#{$i}) {
                                    svg {
                                        color: darken(nth($colors, random(length($colors))), 15%);
                                    }
                                    a {
                                        &:hover {
                                            span {
                                                color: darken(nth($colors, random(length($colors))), 15%);
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .sidebar-header {
                transition: all 0.3s ease;
            }
        }
        &.sidebar-icon {
            .page-sidebar {
                &.light-sidebar {
                    .sidebar-menu {
                        li {
                            &:hover {
                                .single-header {
                                    background-color: $light-gray !important;
                                }
                                .sidebar-submenu {
                                    background-color: $light-gray !important;
                                }
                            }
                            a:hover+ul {
                                background-color: $light-gray !important;
                            }
                            ul {
                                li {
                                    &:hover {
                                        .sidebar-submenu {
                                            background-color: $light-gray !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.color1-sidebar {
                    .sidebar-menu {
                        li {
                            &:hover {
                                .single-header {
                                    background-color: $color1 !important;
                                }
                                .sidebar-submenu {
                                    background-color: $color1 !important;
                                }
                            }
                            a:hover+ul {
                                background-color: $color1 !important;
                            }
                            ul {
                                li {
                                    &:hover {
                                        .sidebar-submenu {
                                            background-color: $color1 !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.color2-sidebar {
                    .sidebar-menu {
                        li {
                            &:hover {
                                .single-header {
                                    background-color: $color2 !important;
                                }
                                .sidebar-submenu {
                                    background-color: $color2 !important;
                                }
                            }
                            a:hover+ul {
                                background-color: $color2 !important;
                            }
                            ul {
                                li {
                                    &:hover {
                                        .sidebar-submenu {
                                            background-color: $color2 !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.color3-sidebar {
                    .sidebar-menu {
                        li {
                            &:hover {
                                .single-header {
                                    background-color: $color3 !important;
                                }
                                .sidebar-submenu {
                                    background-color: $color3 !important;
                                }
                            }
                            a:hover+ul {
                                background-color: $color3 !important;
                            }
                            ul {
                                li {
                                    &:hover {
                                        .sidebar-submenu {
                                            background-color: $color3 !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.color4-sidebar {
                    .sidebar-menu {
                        li {
                            &:hover {
                                .single-header {
                                    background-color: $color4 !important;
                                }
                                .sidebar-submenu {
                                    background-color: $color4 !important;
                                }
                            }
                            a:hover+ul {
                                background-color: $color4 !important;
                            }
                            ul {
                                li {
                                    &:hover {
                                        .sidebar-submenu {
                                            background-color: $color4 !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.color5-sidebar {
                    .sidebar-menu {
                        li {
                            &:hover {
                                .single-header {
                                    background-color: $color5 !important;
                                }
                                .sidebar-submenu {
                                    background-color: $color5 !important;
                                }
                            }
                            a:hover+ul {
                                background-color: $color5 !important;
                            }
                            ul {
                                li {
                                    &:hover {
                                        .sidebar-submenu {
                                            background-color: $color5 !important;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

//Dark layout
body {
    &.light-only {
        .page-wrapper.horizontal-wrapper {
            .page-body-wrapper {
                .page-body {
                    background-color: $light;
                    ul.the-icons {
                        li {
                            color: $black;
                            display: inline-block;
                            padding: 10px;
                            &:hover {
                                background: $black;
                                box-shadow: 0 0 3px $dark-card-background;
                                color: $white;
                            }
                            em {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }
    &.dark-only {
        color: $dark-all-font-color;
        background-color: $dark-body-background;
        // display:none;
        .blog-box {
            h6 {
                color: $white;
            }
            .blog-details-second {
                .detail-footer {
                    border-top: 1px solid $dark-body-background;
                    ul.sociyal-list li:nth-child(n+2) {
                        border-left: 1px solid $dark-body-background;
                    }
                }
            }
        }
        a.cke_button {
            filter: brightness(0) invert(1);
        }
        .cke_combo_text {
            color: $white;
        }
        .job-search .job-description {
            border-top: 1px solid $dark-body-background;
        }
        .job-sidebar {
            .job-left-aside {
                background-color: $dark-card-background;
                border-color: $dark-card-border
            }
        }
        .job-search .job-description .theme-form {
            border-bottom: 1px solid $dark-body-background;
        }
        .categories ul li:hover {
            background-color: $dark-body-background;
        }
        .blog-list .blog-details h6 {
            color: $white;
        }
        .default-according {
            .card-header {
                background-color: $dark-card-background !important;
            }
        }
