                            }
                        }
                    }
                }
                &.income-card {
                    .card-body {
                        .knob-block {
                            input {
                                color: $dark-all-font-color !important;
                            }
                        }
                    }
                }
            }
            .latest-update-sec {
                table {
                    tbody {
                        tr {
                            td {
                                img {
                                    filter: brightness(0) invert(1);
                                }
                            }
                        }
                    }
                }
            }
            span,
            p,
            h5,
            h4,
            h5,
            h6,
            th,
            h3,
            a {
                color: $dark-all-font-color;
            }
            .recent-order-sec {
                table {
                    tr {
                        td {
                            p {
                                color: $dark-all-font-color;
                            }
                        }
                    }
                }
            }
        }
        .setting-list {
            background-color: $dark-card-background;
        }
        .latest-update-sec {
            .media {
                img,
                svg {
                    fill: $dark-small-font-color;
                }
            }
        }
        .dashboard-default-sec {
            .card {
                .card-header {
                    ul.week-date {
                        li:nth-child(n+2) {
                            border-color: $dark-card-border;
                        }
                    }
                }
            }
        }
        .top-dealer-sec {
            .card {
                .top-dealerbox {
                    &:hover {
                        background-color: $dark-body-background;
                    }
                }
            }
        }
        // dashboard-2 dark css start
        .activity-sec {
            .activity-timeline {
                .media {
                    .activity-line {
                        background-color: #374558;
                    }
                    .media-body {
                        p {
                            color: $dark-all-font-color;
                        }
                    }
                }
                &.update-line {
                    .media {
                        .media-body {
                            .tomorrow-sec {
                                p {
                                    color: $dark-all-font-color;
                                    background-color: $dark-card-background;
                                }
                                &:before {
                                    background-color: $dark-card-border;
                                }
                            }
                        }
                    }
                }
            }
        }
        .activity-dot-primary {
            background-color: $dark-card-background;
        }
        //user profile
        .user-profile {
            .profile-header {
                .userpro-box {
                    background-color: $dark-card-background;
                    h4 {
                        color: $dark-all-font-color;
                    }
                    .icon-wrapper {
                        background-color: $dark-body-background;
                    }
                }
            }
            .follow {
                .follow-num {
                    color: $dark-all-font-color;
                }
                ul.follow-list {
                    border-color: $dark-card-border;
                    li {
                        &:nth-child(n+2) {
                            border-color: $dark-card-border
                        }
                    }
                }
            }
            .post-about {
                ul {
                    li {
                        h5 {
                            color: $dark-all-font-color;
                        }
                    }
                }
            }
            .profile-post {
                .post-header {
                    .media {
                        h5 {
                            color: $dark-all-font-color;
                        }
                    }
                }
                .post-body {
                    .post-react {
                        h6 {
                            color: $dark-all-font-color;
                        }
                        p {
                            color: $dark-small-font-color
                        }
                    }
                }
            }
        }
        .social-tab {
            .input-group {
                .form-control {
                    border-color: $dark-card-border
                }
            }
        }
        // widget //
        .cal-date-widget {
            .datepicker {
                background-color: $dark-card-border;
                border: none;
                .datepicker--cell-day {
                    color: $white;
                }
            }
        }
        .datepicker--day-name {
            color: $primary-color;
        }
        .product-social {
            li {
                a {
                    border-color: $dark-card-border;
                }
            }
        }
        .page-wrapper,
        .compact-wrapper {
            .page-body-wrapper {
                .page-body {
                    .widget-joins {
                        &:before,
                        &:after {
                            background-color: $dark-card-border !important;
                        }
                    }
                }
            }
            &.compact-wrapper {
                .page-body-wrapper {
                    header {
                        &.main-nav {
                            .sidebar-main-title {
                                >div {
                                    border-color: $dark-card-border;
                                    p {
                                        color: $dark-all-font-color;
                                    }
                                }
                            }
                            .main-navbar {
                                .nav-menu {
                                    >li {
                                        .nav-link {
                                            svg {
                                                color: $dark-all-font-color !important;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .product-page-main {
                thead,
                tbody,
                tfoot,
                tr,
                td,
                th {
                    border-color: transparent;
                    background-color: transparent;
                }
                p {
                    color: $dark-small-font-color;
                }
            }
            thead,
            tbody,
            tfoot,
            tr,
            td,
            th {
                border-color: $dark-card-border;
            }
        }
        .testimonial {
            i {
                color: $primary-color !important;
            }
        }
        .blockquote {
            border-color: $dark-card-border;
        }
        .datepicker,
        .daterangepicker {
            background-color: $dark-card-background;
            border-color: $dark-card-border;
        }
        .daterangepicker {
            &:after {
                border-bottom-color: $dark-card-border;
            }
            .input-mini {
                &.active {
                    border: 1px solid $dark-border-color;
                }
            }
        }
        .daterangepicker {
            .calendar-table {
                background-color: $dark-card-background;
                border: none;
            }
        }
        .daterangepicker.dropdown-menu {
            background-color: $dark-card-background;
        }
        .datepicker--nav {
            border-color: $dark-card-border;
        }
        .datepicker--cell-day {
            color: $dark-all-font-color
        }
        .datepicker--cell-day,
        .datepicker--cell-year {
            &.-other-month-,
            .-other-decade- {
                color: $dark-small-font-color;
            }
        }
        .date-range-picker {
            .daterange-card {
                border-color: $dark-card-border;
            }
        }
        .daterangepicker {
            td {
                &.off,
                &.in-range,
                &.start-date,
                &.end-date {
                    background-color: $dark-body-background;
                    color: $dark-all-font-color;
                }
            }
            .input-mini {
                border-color: $dark-card-border !important;
                background-color: $dark-card-background;
            }
            &.ltr {
                .calendar-time {
                    select {
                        background-color: $dark-card-background;
                        border-color: $dark-card-border;
                    }
                }
            }
        }
        .ranges {
            li {
                background-color: $dark-card-background;
                border-color: $dark-card-border;
            }
        }
        .f1 {
            .f1-steps {
                .f1-progress {
                    background-color: $dark-border-color;
                }
                .f1-step {
                    .f1-step-icon {
                        background-color: $dark-body-background;
                    }
                    &.active {
                        .f1-step-icon {
                            background-color: $primary-color;
                        }
                    }
                }
            }
        }
        //card //
        .b-b-info {
            border-color: $dark-card-border !important;
        }
        // sidebar 
        .main-navbar {
            ul.nav-pills {
                border-color: $dark-card-border;
                .nav-item {
                    &:last-child {
                        border-color: $dark-card-border;
                    }
                    .nav-link {
                        color: $dark-all-font-color;
                    }
                }
            }
        }
        .messanger-chat {
            .chat {
                .message-main {
                    p {
                        color: $dark-card-border;
                    }
                }
            }
        }
        // project list
        .project-list {
            .border-tab {
                border: none !important;
                &.nav-tabs {
                    .nav-item {
                        .nav-link {
                            border: none;
                        }
                    }
                }
            }
        }
        .project-box {
            background-color: $dark-body-background;
            border-color: $dark-card-border
        }
        .file-content {
            .form-control-plaintext {
                color: $dark-all-font-color;
                &:focus {
                    color: $dark-all-font-color;
                }
            }
            .form-inline {
                border-color: $dark-card-border;
            }
            .files {
                .file-box {
                    border-color: $dark-card-border;
                    .file-top {
                        background-color: $dark-body-background;
                        border-color: $dark-border-color;
                    }
