                            }
                        }
                    }
                }
            }
        }
        .page-wrapper {
            .page-body-wrapper {
                background-color: $dark-card-background
            }
            &.horizontal-wrapper {
                .page-body-wrapper {
                    &.horizontal-menu {
                        header {
                            &.main-nav {
                                background-color: $dark-card-background;
                                border-color: $dark-card-border;
                                .main-navbar {
                                    .nav-menu {
                                        >li {
                                            +li {
                                                border-color: $dark-card-border;
                                            }
                                            >a {
                                                color: $dark-all-font-color;
                                            }
                                        }
                                    }
                                    .left-arrow,
                                    .right-arrow {
                                        background-color: $dark-card-background;
                                        border-color: $dark-card-border
                                    }
                                }
                            }
                        }
