            .page-body-wrapper {
                .sidebar {
                    box-shadow: 0 0 11px $dark-card-background;
                }
            }
            $alert-name:primary,
            secondary,
            success,
            danger,
            warning,
            info,
            light,
            dark;
            $alert-color :$primary-color,
            $secondary-color,
            $success-color,
            $danger-color,
